import { useState, useEffect } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { format } from 'date-fns'
import { DiaryEditor } from '../components/DiaryEditor'
import { PhotoUpload } from '../components/PhotoUpload'
import { ChevronLeftIcon, CalendarIcon, XMarkIcon, ChevronLeftIcon as ArrowLeftIcon, ChevronRightIcon as ArrowRightIcon } from '@heroicons/react/24/outline'
import { useData } from '../contexts/DataContext'

export function DiaryPage() {
  const { getDiaryByDate, getContextByDate, isUsingMockData } = useData()
  const { date: dateParam } = useParams()
  const navigate = useNavigate()
  const [selectedDate, setSelectedDate] = useState<Date>(new Date())
  const [content, setContent] = useState('')
  const [photos, setPhotos] = useState<File[]>([])
  const [title, setTitle] = useState('')
  const [selectedPhotoIndex, setSelectedPhotoIndex] = useState<number | null>(null)
  
  // 날짜가 변경될 때 해당 날짜의 일기 데이터 불러오기
  useEffect(() => {
    if (dateParam) {
      const parsedDate = new Date(dateParam)
      if (!isNaN(parsedDate.getTime())) {
        setSelectedDate(parsedDate)
      }
    }
  }, [dateParam])

  // 선택된 날짜의 일기 데이터 불러오기
  useEffect(() => {
    const dateString = selectedDate.toISOString().split('T')[0]
    const existingDiary = getDiaryByDate(dateString)
    if (existingDiary) {
      setTitle(existingDiary.title)
      setContent(existingDiary.content)
      // 실제 구현에서는 photos도 로드해야 하지만, 더미 데이터에서는 URL만 있음
      setPhotos([])
    } else {
      setTitle('')
      setContent('')
      setPhotos([])
    }
  }, [selectedDate])

  const handleSave = async () => {
    // TODO: Firebase에 저장 로직 구현
    console.log('Saving diary:', { date: selectedDate, content, photos })
    alert('일기가 저장되었습니다!')
  }

  const handleDateChange = (newDate: Date) => {
    setSelectedDate(newDate)
    const dateString = format(newDate, 'yyyy-MM-dd')
    navigate(`/diary/${dateString}`)
  }

  // 현재 선택된 날짜의 일기 데이터
  const currentDiary = getDiaryByDate(selectedDate.toISOString().split('T')[0])

  // 사진 모달 관련 함수들
  const openPhotoModal = (index: number) => {
    setSelectedPhotoIndex(index)
  }

  const closePhotoModal = () => {
    setSelectedPhotoIndex(null)
  }

  const navigatePhoto = (direction: 'prev' | 'next') => {
    if (!currentDiary?.photos || selectedPhotoIndex === null) return
    
    if (direction === 'prev') {
      setSelectedPhotoIndex(selectedPhotoIndex > 0 ? selectedPhotoIndex - 1 : currentDiary.photos.length - 1)
    } else {
      setSelectedPhotoIndex(selectedPhotoIndex < currentDiary.photos.length - 1 ? selectedPhotoIndex + 1 : 0)
    }
  }

  // 키보드 이벤트 처리
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (selectedPhotoIndex === null) return
      
      switch (e.key) {
        case 'Escape':
          closePhotoModal()
          break
        case 'ArrowLeft':
          navigatePhoto('prev')
          break
        case 'ArrowRight':
          navigatePhoto('next')
          break
      }
    }

    window.addEventListener('keydown', handleKeyDown)
    return () => window.removeEventListener('keydown', handleKeyDown)
  }, [selectedPhotoIndex])

  return (
    <div className="max-w-4xl mx-auto">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <button
          onClick={() => navigate(-1)}
          className="flex items-center gap-2 text-gray-600 hover:text-gray-900"
        >
          <ChevronLeftIcon className="w-5 h-5" />
          돌아가기
        </button>
        
        <div className="flex items-center gap-4">
          <div className="flex items-center gap-2">
            <CalendarIcon className="w-5 h-5 text-gray-600" />
            <input
              type="date"
              value={format(selectedDate, 'yyyy-MM-dd')}
              onChange={(e) => handleDateChange(new Date(e.target.value))}
              className="border border-gray-300 rounded-md px-3 py-1 text-sm"
            />
          </div>
          
          <button
            onClick={handleSave}
            className="btn-primary"
          >
            저장
          </button>
        </div>
      </div>

      {/* Date Display */}
      <div className="card mb-6">
        <h1 className="text-2xl font-bold text-gray-900 mb-2">
          {format(selectedDate, 'yyyy년 M월 d일')}
        </h1>
        <p className="text-gray-600 mb-2">
          {format(selectedDate, 'EEEE')}
        </p>
        {title && (
          <h2 className="text-lg font-medium text-blue-700 mt-2">
            {title}
          </h2>
        )}
      </div>

      {/* Main Content */}
      <div className="grid lg:grid-cols-3 gap-6">
        {/* Diary Editor */}
        <div className="lg:col-span-2">
          <div className="card">
            <h2 className="text-lg font-semibold mb-4">일기 작성</h2>
            <DiaryEditor
              content={content}
              onChange={setContent}
              placeholder="오늘 하루는 어땠나요? 자유롭게 기록해보세요..."
            />
          </div>
        </div>

        {/* Sidebar */}
        <div className="space-y-6">
          {/* 기존 일기 사진들 표시 */}
          {currentDiary && currentDiary.photos && currentDiary.photos.length > 0 && (
            <div className="card">
              <h3 className="font-medium mb-3">일기 사진 ({currentDiary.photos.length}장)</h3>
              <div className="grid grid-cols-1 gap-3">
                {currentDiary.photos.map((photoUrl, index) => (
                  <div key={index} className="relative group">
                    <img
                      src={photoUrl}
                      alt={`일기 사진 ${index + 1}`}
                      className="w-full h-32 object-cover rounded-lg border border-gray-200 shadow-sm hover:shadow-md transition-all cursor-pointer"
                      onClick={() => openPhotoModal(index)}
                    />
                    <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-20 transition-all rounded-lg flex items-center justify-center opacity-0 group-hover:opacity-100 pointer-events-none">
                      <span className="text-white text-sm font-medium bg-black bg-opacity-50 px-2 py-1 rounded">
                        크게 보기
                      </span>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}

          {/* Photo Upload */}
          <div className="card">
            <h3 className="font-medium mb-3">새 사진 추가</h3>
            <PhotoUpload
              photos={photos}
              onPhotosChange={setPhotos}
            />
          </div>

          {/* Context Info */}
          <div className="card">
            <h3 className="font-medium mb-3">오늘의 맥락</h3>
            <div className="space-y-3 text-sm">
              <div>
                <span className="text-gray-600">날씨:</span>
                <span className="ml-2">
                  {currentDiary?.weather || '로딩 중...'}
                </span>
              </div>
              <div>
                <span className="text-gray-600">기분:</span>
                <span className="ml-2">
                  {currentDiary?.mood ? 
                    {'great': '😊 최고', 'good': '😌 좋음', 'neutral': '😐 보통', 'bad': '😞 안좋음', 'terrible': '😢 최악'}[currentDiary.mood] || '😐 보통'
                    : '기록 없음'
                  }
                </span>
              </div>
              <div>
                <span className="text-gray-600">태그:</span>
                <div className="mt-1">
                  {currentDiary?.tags?.map(tag => (
                    <span key={tag} className="inline-block bg-blue-100 text-blue-800 text-xs px-2 py-1 rounded-full mr-1 mb-1">
                      #{tag}
                    </span>
                  )) || <span className="text-gray-400">태그 없음</span>}
                </div>
              </div>
            </div>
          </div>

          {/* Quick Stats */}
          <div className="card">
            <h3 className="font-medium mb-3">통계</h3>
            <div className="space-y-2 text-sm">
              <div className="flex justify-between">
                <span className="text-gray-600">작성한 글자 수</span>
                <span className="font-medium">{content.length}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">첨부 사진</span>
                <span className="font-medium">{photos.length}장</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* 사진 모달 */}
      {selectedPhotoIndex !== null && currentDiary?.photos && (
        <div 
          className="fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center z-50"
          onClick={closePhotoModal}
        >
          <div 
            className="relative max-w-4xl max-h-screen p-4"
            onClick={(e) => e.stopPropagation()}
          >
            {/* 닫기 버튼 */}
            <button
              onClick={closePhotoModal}
              className="absolute top-4 right-4 z-10 bg-black bg-opacity-50 text-white p-2 rounded-full hover:bg-opacity-75 transition-all"
            >
              <XMarkIcon className="w-6 h-6" />
            </button>

            {/* 이전/다음 버튼 */}
            {currentDiary.photos.length > 1 && (
              <>
                <button
                  onClick={(e) => {
                    e.stopPropagation()
                    navigatePhoto('prev')
                  }}
                  className="absolute left-4 top-1/2 transform -translate-y-1/2 z-10 bg-black bg-opacity-50 text-white p-3 rounded-full hover:bg-opacity-75 transition-all"
                >
                  <ArrowLeftIcon className="w-6 h-6" />
                </button>
                <button
                  onClick={(e) => {
                    e.stopPropagation()
                    navigatePhoto('next')
                  }}
                  className="absolute right-4 top-1/2 transform -translate-y-1/2 z-10 bg-black bg-opacity-50 text-white p-3 rounded-full hover:bg-opacity-75 transition-all"
                >
                  <ArrowRightIcon className="w-6 h-6" />
                </button>
              </>
            )}

            {/* 사진 */}
            <img
              src={currentDiary.photos[selectedPhotoIndex]}
              alt={`일기 사진 ${selectedPhotoIndex + 1}`}
              className="max-w-full max-h-full object-contain rounded-lg"
            />

            {/* 사진 정보 */}
            <div className="absolute bottom-4 left-4 right-4 bg-black bg-opacity-50 text-white p-3 rounded-lg">
              <div className="text-center">
                <div className="text-sm font-medium">
                  {selectedPhotoIndex + 1} / {currentDiary.photos.length}
                </div>
                <div className="text-xs text-gray-300 mt-1">
                  {format(selectedDate, 'yyyy년 M월 d일')} - {title}
                </div>
                <div className="text-xs text-gray-400 mt-2">
                  ESC: 닫기 | ←→: 이전/다음 사진
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
